.ThingsScreen {
  position: relative; }

.ThingsScreen__mapWrapper {
  height: 30vh;
  background-color: #B0B0B0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  -webkit-transition: height 500ms ease;
  -o-transition: height 500ms ease;
  transition: height 500ms ease; }
  .ThingsScreen--fullScreenMap .ThingsScreen__mapWrapper {
    height: calc(100vh - 100px); }

.ThingsScreen__eventsWrapper {
  padding-top: 30vh; }

.ThingsScreen__eventsTabs {
  background-color: #FFFFFF;
  display: -ms-flexbox;
  display: flex; }

.ThingsScreen__eventsTabs__tab {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  text-align: center;
  padding: 15px 10px;
  position: relative; }
  .ThingsScreen__eventsTabs__tab::after {
    content: '';
    position: absolute;
    left: 10px;
    right: 10px;
    bottom: 0;
    height: 0;
    background-color: #65bfff;
    -webkit-transition: height 300ms ease;
    -o-transition: height 300ms ease;
    transition: height 300ms ease; }
  .ThingsScreen__eventsTabs__tab.ThingsScreen__eventsTabs__tab--selected::after {
    height: 5px; }

.ThingsScreen__eventsList__group {
  margin-top: 30px; }

.ThingsScreen__eventsList__group__title {
  margin: 0 20px; }



